.App {
  max-width: 860px;
  margin: 40px auto;
}
.game-settings {
  margin: 0 auto;
  width: fit-content;
  padding: 1rem;
  border: 2px solid;
  border-radius: 1rem;
  background-color: #2c1e3e;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23866;
  color: #fff;
}
.card-grid {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card-grid-medium {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.card-grid-hard {
  margin: 40px 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 2rem;
}

.select-difficulty {
  margin: 20px 0 0;
  font-family: Raleway, sans-serif;
  padding: 5px;
  font-size: 1.5rem;
  background-color: #2c1e3e;
  color: white;
  border-radius: 10px;
  outline: none;
}

